* {
    margin: 0;
    padding: 0;
    font-weight: bold;
  }
.background {
    width: 100%;
    height: 100vh;
    background: #45154f;
    position: relative;
}
.container {
    position: relative;
    width: 100%;
    top: 50px;
}
.fetch_button {
    height: 50px;
    width: 150px;
    border-radius: 10px;
    cursor: pointer;
    background: #477ccc;
    border: 1px solid black;
    margin-top: 15px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
}
.fetch_button:focus {
    outline: none;
}
.picture_container {
    width: 400px;
    height: 400px;
    position: relative;
    border: 10px solid #fff;
    left: 50%;
    transform: translateX(-50%);
}
.image_dog {
    position: absolute;
    width: 100%;
    height: 100%;
}
.empty_picture {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    text-align: center;
    color: #fff;
    font-size: 32px;
}

.glow-on-hover {
    width: 220px;
    height: 50px;
    border: none;
    outline: none;
    color: #fff;
    background: #45154f;
    cursor: pointer;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 40px;
    border-radius: 10px;
}

.glow-on-hover:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 1;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
    outline: none;
}

.glow-on-hover:active {
    color: #45154f;
    outline: none;
}
.glow-on-hover:focus {
    outline: none;
}
.glow-on-hover:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #45154f;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}

.title_text {
    color: #fff;
    font-size: 32px;
    text-align: center;
    margin-bottom: 40px;
}

@media (max-width: 660px) {
    .container {
        top: 150px;
    }
    .picture_container {
        width: 80%;
        height: 300px;
    }
}
